<template>
  <div class="footerBtn">
    <img
      v-if="attach.laterUrl"
      :src="attach.laterUrl"
      class="preview"
      style="width:200px;height:200px"
    />
    <el-button class="btn-edit" icon="el-icon-edit" @click="dialogVisible=true"></el-button>

    <!-- - -->
    <el-dialog title="" :visible.sync="dialogVisible" :before-close="handleClose">
      <span>
        <el-row>
          <input
            type="file"
            id="uploads"
            accept="image/png, image/jpeg, image/gif, image/jpg"
            @change="uploadImg($event,1)"
            class="el-button"
            style="margin-bottom:15px"
          />
        </el-row>
        <el-row>
          <el-col :span="16">
            <!--  -->
            <vueCropper
              style="width:100%;height:300px"
              ref="cropper"
              :img="attach.customaryUrl"
              :autoCrop="options.autoCrop"
              :fixedBox="options.fixedBox"
              :canMoveBox="options.canMoveBox"
              :autoCropWidth="options.autoCropWidth"
              :autoCropHeight="options.autoCropHeight"
              :centerBox="options.centerBox"
              :fixed="options.fixed"
              @realTime="realTime"
            ></vueCropper>
          </el-col>
          <el-col :span="8">
            <h2 align="center"></h2>
            <div class="show-preview">
              <div :style="previews.div" class="preview">
                <img :src="previews.url" :style="previews.img" />
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row class="footerBtn" align="center">
          <el-button type="primary" size="small" round @click="cut('blob')"></el-button>
          <el-button type="primary" size="small" round @click="handleClose"></el-button>
        </el-row>
      </span>
    </el-dialog>
  </div>
</template>

<script>
//
import { VueCropper }  from 'vue-cropper' 
import api from "@/api/index.js";

export default {
  components: {
      VueCropper,
    },
  data() {
    return {
      dialogVisible: false,
      options: {
        autoCrop: true, //
        fixedBox: false, //
        canMoveBox: true, //
        autoCropWidth: 200, //
        autoCropHeight: 200, //
        centerBox: false, //
        fixed:true
      },
      previews: {}, //
      attach: {
        //
        id: "",
        userId: "",
        customaryUrl: "", //
        laterUrl: "", //  /static/logo.png
        attachType: "photo" //
      },
      fileName: "", //
      uploadImgRelaPath: "" //
    };
  },
  methods: {
    //
    handleClose() {
      this.dialogVisible = false;
    },
    //
    realTime(data) {
      this.previews = data;
    },
    //
    find() {
      this.userId = sessionStorage.getItem("userId");
      this.$axios.post("/api/attach/find", this.attach).then(res => {
        // console.log(res);
      });
    },
    //
    uploadImg(e, num) {
      var file = e.target.files[0];
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
        this.$message.error(".gif,jpeg,jpg,png,bmp");
        return false;
      }
      // console.log(e.target.result);
      //fileReader 
      var reader = new FileReader();
      reader.readAsDataURL(file); // dataURL
      reader.onload = e => {
        // data = window.URL.createObjectURL(new Blob([e.target.result])) blob

        let data = e.target.result;

        this.attach.customaryUrl = data;
        // base64
        // reader.readAsDataURL(file)
        // blob
      };
    },
    //,
    cut(type) {
      this.$refs.cropper.getCropData(res => {
        //resbolb
        api.changePortrait({
            "id":1234,
            "type":"portrait",
            "subtype":"upload",
            "data":{
              "base64":res
              }
            }).then(res => {
              console.log(res)
              //this.$router.go(0)
            })
      });
    }
  }
};
</script>

<style lang="scss" scoped>
/*   */
.show-preview {
  display: flex;
  justify-content: center;
}

.preview {
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid #cccccc;
  background: #cccccc;
}
.footerBtn {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  .btn-edit{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    position: absolute;
    left: 270px;
    top: 220px;
    font-size: 20px;
  }
}
</style>
